import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { Router } from "@angular/router";
import { AuthentificationService } from "src/app/services/authentification/authentification.service";
import { HttpClient, HttpHeaders } from "@angular/common/http";

const httpOptions = {
  headers: new HttpHeaders({
    "Content-Type": "application/json",
    Authorization: "my-auth-token",
  }),
};

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"],
})
export class LoginComponent implements OnInit {
  loading = false;
  submitted = false;
  usernameControl = new FormControl(
    "",
    Validators.compose([Validators.maxLength(12), Validators.required])
  );

  returnUrl: string;
  loginForm: FormGroup;

  constructor(
    formBuilder: FormBuilder,
    private router: Router,
    private authService: AuthentificationService,
    public http: HttpClient
  ) {
    this.loginForm = formBuilder.group({
      username: this.usernameControl,
    });
  }

  ngOnInit(): void {}

  login(): void {
    if (this.loginForm.valid) {
      this.authService.username = this.usernameControl.value;
      this.router.navigate(["/chat"]);

      const body = { username: this.authService.username };
      //this.http.post<any>('https://test-23sdfg23.herokuapp.com/database/insert', body, httpOptions).subscribe(() => {});
    }
  }
}
